$sub-color: grey;

@mixin shrinkLabel {
  top: -14px;
  font-size: 12px;
  color: var(--default-text-color);
}

.group {
  position: relative;
  margin: 45px 0;

  .form-input {
    background: none;
    background-color: white;
    color: $sub-color;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid var(--default-border);
    margin: 25px 0;
    background: var(--sub-color);
    
    input:-internal-autofill-previewed + path {
      fill: var(--default-bg) !important;
    }

    &:focus {
      outline: none;
    }

    &:focus ~ .form-input-label {
      @include shrinkLabel();
    }
  }

  input[type='password'] {
    letter-spacing: 0.3em;

  }

  .form-input-label {
    color: $sub-color;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 14px;
    transition: 300ms ease all;

    &.shrink {
      @include shrinkLabel();
    }
  }
}
